    techstack: [
      "HTML",
      "CSS",
      "javascript",
      "Reactjs",
      "Redux",
      "Chakraui",
      "MongoDB",
      "Express",
      "nodejs",
    ],
    deploy: "https://frontend-phi-five-95.vercel.app/",
