import React from 'react'
import "./home.css"
import {HiOutlineArrowCircleDown} from 'react-icons/hi';
import ME from "../../assert/me1.png"
import CV from "../../assert/cv.pdf"


const Home = () => {
  return (
    <div id="home">
        <div class="detail">
            <h2 >Hi, 👋 I am<br/> <span id="user-detail-name">Sumit Kumar</span></h2>
            <p id="user-detail-intro">
               I am a Fullstack web developer with a focus on the MERN stack.
               I have more than 1200 hours of coding experience, working on various projects,
                 both as part of a team and as an individual.
               I am passionate about web development and
                am always looking to improve my skills. 
            </p>
            
              <button id="resume-button-2" onClick={() =>window.open(CV)}>
                  <a href={CV} download="Sumit-Kumar-Resume" id="resume-link-2" target="_blanck">
                      Resume <HiOutlineArrowCircleDown class="d_icone"/>
                  </a>
              </button>
            
        </div>
        
        <div className='container__img'>
            <div className='pro__img'>
                <img class='home-img' src={ME} alt="About" />
            </div>
         </div>
      </div>
    
  )
}

export default Home